@import '../common/vars.less';
@import '../common/mixins.less';


.header {
  position: absolute;
  top: 0;
  height: 0;
  width: 100%;

  @media screen and (max-width: 768px) {

    &::before {
      @k: 94 / 85;
      @w: 80px;
      @h: @w / @k;

      content: '';
      position: absolute;
      width: @w;
      height: @h;
      top: 30px;
      left: 50%;
      transform: translateX(-50%);
      background: url(img/svg/logo.svg) no-repeat;
      background-size: contain;
      
    }


    &__nav {

      @margin: 100px;
      @margin-10: 70px;
      @margin-1: 50px;
      @margin-2: 20px;

      position: fixed;
      top: 0;
      height: 0;
      width: 100%;
      height: 100%;
      background-color: @color-black-1;
      z-index: 999;

      opacity: 0;
      transform: translateX(100%);
      transition: all .2s linear;

      & .container {
        .set-flex(nowrap; center; center);
        height: 100%;
      }

      & .menu {
        display: block;
        padding: 0;

        &__logo{
          position: inherit;
          display: block;
          text-align: center;
          margin: @margin 0;

          @media (max-height: 670px) {
            margin: @margin-10;            
          }
          @media (max-height: 650px) {
            margin: @margin-1;            
          }
          @media (max-height: 450px) {
            margin: @margin-2;            
          }
        }

        &__list {
          display: block;
          text-align: center;

          & .menu__item {
            margin: 0;

            &:first-child {
              margin-bottom: @margin;

              @media (max-height: 670px) {
                margin: @margin-10;            
              }

              @media (max-height: 650px) {
                margin: @margin-1;            
              }
    
              @media (max-height: 450px) {
                margin: @margin-2;            
              }
    

            }

            & .menu__link {
              font-size: 32px;
              font-weight: 400;
              color: @color-white-2;

              @media (max-width: 500px), (max-height: 650px)  {
                font-size: 26px;
              }
            }

          }

          & .menu__item:nth-child(2) {
            display: none;
          }
        };
      }

      .burger {
        display: none;
      }

      .header__close {
        @size: 40px;

        position: absolute;
        width: @size;
        height: @size;
        top: @size - 10;
        right: @size - 10;
        cursor: pointer;
      
        &-line {
          .set-absolute(@bgc:  @color-white-2; @height: 2px; @opc: 1);
          top: 50%;
      
          &:first-child{
            transform: rotate(45deg);
          }
      
          &:last-child {
            transform: rotate(-45deg);
          }
        }
      
        &:hover {
          .link-hover-opc();
        }





      }

      &_active {
        opacity: 1;
        transform: translateX(0);
      }
 

    }
    
  }

}

.menu {
  max-width: 618px;
  .set-flex(@content: space-between; @align: baseline);
  padding-top: 35px;
  margin: 0 auto;

  @media screen and (max-width: 768px) {
  .set-flex(@content: center; @align: center);
  }

}

.menu__list {
  .set-flex();
  
  &:last-child {
    justify-content: flex-end;
  }

  @media screen and (max-width: 768px) {
    display: none; 
  }

}  

.menu__item {
  &:not(:last-child) {
    margin-right: 30px;
  }

}

.menu__link {
  .set-font(@size: 13px, @weight: 700, @color: @color-black-4);
  text-transform: uppercase;
  letter-spacing: 2.6px;
  display: block;

  &:hover {
    .link-hover-opc();
  }

}

