@import './vars.less';
@import './mixins.less';

body {
  .font-lato();
  .set-font();
}

.container {
  max-width: @cnt-width + @cnt-padding;
  padding: 0 @cnt-padding / 2;
  margin:  0 auto;
}

.card-title {
  .font-lato();
  .set-font(18px, 700, @font-style, 12px, @color: @color-black-3 );
  letter-spacing: 3.6px;
  text-transform: uppercase;

  @media screen and (max-width: 1000px) {
    font-size: 15px;
    letter-spacing: 2px;
  }

}

.picture-container {  
  position: relative;
  height: 100%;

  &__logo a {
    .set-logo-link ();
  }
}