@import '../../common/vars.less';
@import '../../common/mixins.less';


@max-width-1000: 1000px;
@max-width-768: 768px;
@max-width-600: 600px;
@max-width-400: 400px;
@max-width-300: 300px;


.adv {

  // adv__header
  &__header {
    position: relative;
    padding: 124px 0 108px 0;
    background-color: @color-white-2;

    @media screen and (max-width: 1200px) {
      padding: 84px 0 100px 0;
    }

    @media screen and (max-width: 1000px) {
      padding: 66px 0 90px 0;
    }

    @media screen and (max-width: 768px) {
      padding: 66px 0 66px 0;
    }

    & input[type="radio"] {
      display: none;
    }

    /////////////////////////////

    @input: adv__radio-;
    @slider-width: @cnt-total-width;

    input[id="@{input}f"]:checked ~ ul li:nth-child(1) label{
      background-color: @color-gray-3;
    }

    input[id="@{input}s"]:checked ~ ul li:nth-child(2) label{
      background-color: @color-gray-3;
    }

    input[id="@{input}t"]:checked ~ ul li:nth-child(3) label{
      background-color: @color-gray-3;
    }

    ///////////////////////

    input[id="@{input}f"]:checked ~ .adv__slider .adv__slider-container{
      margin-left: 0;
    }

    input[id="@{input}s"]:checked ~ .adv__slider .adv__slider-container{
      margin-left: -@slider-width;

      @media screen and (max-width: 1200px) {
        margin-left: -@max-width-1000;
      }
      @media screen and (max-width: 1000px) {
        margin-left: -@max-width-768;
      }
      @media screen and (max-width: 768px) {
        margin-left: -@max-width-600;
      }
      @media screen and (max-width: 600px) {
        margin-left: -@max-width-400;
      }
      @media screen and (max-width: 400px) {
        margin-left: -@max-width-300;
      }
    }

    input[id="@{input}t"]:checked ~ .adv__slider .adv__slider-container{
      margin-left: -@slider-width * 2;

      @media screen and (max-width: 1200px) {
        margin-left: -@max-width-1000 * 2;
      };
      @media screen and (max-width: 1000px) {
        margin-left: -@max-width-768 * 2;
      };
      @media screen and (max-width: 768px) {
        margin-left: -@max-width-600 * 2;
      };
      @media screen and (max-width: 600px) {
        margin-left: -@max-width-400 * 2;
      };
      @media screen and (max-width: 400px) {
        margin-left: -@max-width-300 * 2;
      };
    }

  }
  
  &__header-wrap {
    text-align: center;
  }
  
  &__header-logo {
    margin-bottom: 37px;

    @media screen and (max-width: 1200px) {
      margin-bottom: 27px;
    };
    @media screen and (max-width: 1000px) {
      margin-bottom: 20px;
    };
  }
  
  &__header-title {
    text-transform: none;
    letter-spacing: 1.8px;
    margin-bottom: 27px;

    @media screen and (max-width: 1200px) {
      margin-bottom: 20px;
    }
    @media screen and (max-width: 1000px) {
      margin-bottom: 15px;
    };

    & span {
      color: @color-gray-1;
      font-weight: 300;
    }

  }
  
  &__header-text {
    .set-font(@size: 24px; @style: italic; @line: 34px; @color: @color-gray-1);

    @media screen and (max-width: 1200px) {
      font-size: 20px;
      line-height: 28px;
    }
    @media screen and (max-width: 1000px) {
      font-size: 18px;
      line-height: 22px;
    }
    @media screen and (max-width: 768px) {
      line-height: 20px;
    }
    @media screen and (max-width: 400px) {
      font-size: 16px;
    }

  }

  &__slider {
    position: relative;
    overflow: hidden;
    max-width: @cnt-total-width;
    margin: 0 auto;

    @media screen and (max-width: 1200px) {
      max-width: @max-width-1000;
    }

    @media screen and (max-width: 1000px) {
      max-width: @max-width-768;
    }
    @media screen and (max-width: 768px) {
      max-width: @max-width-600;
    }
    @media screen and (max-width: 600px) {
      max-width: @max-width-400;
    }
    @media screen and (max-width: 400px) {
      max-width: @max-width-300;
    }

    &-container {
      top: 0;
      left: 0;
      width: @cnt-total-width * 3;

      .set-flex(nowrap, flex-start, flex-start);

      transition: all .5s ease;

      .container{
        margin: 0;
      }

      @media screen and (max-width: 1200px) {
        width: @max-width-1000 * 3;
      }
      @media screen and (max-width: 1000px) {
        width: @max-width-768 * 3;
      }
      @media screen and (max-width: 768px) {
        width: @max-width-600 * 3;
      }
      @media screen and (max-width: 600px) {
        width: @max-width-400 * 3;
      }
      @media screen and (max-width: 400px) {
        width: @max-width-300 * 3;
      }
      
    }

  }

  &__slider-list {
    position: absolute;
    top: 390px;
    left: 50%;
    transform: translateX(-50%);

    @media screen and (max-width: 1200px) {
      top: 334px;
    }

    @media screen and (max-width: 1000px) {
      top: 272px;
    }

    @media screen and (max-width: 600px) {
      top: 330px;
    }

    @media screen and (max-width: 400px) {
      top: 366px;
    }

    .set-flex();

    & > li:not(:last-child){
      margin-right: 9px;
    }

    & > li label {
      display: block;
      width: 11px;
      height: 11px;
      border: 1px solid @color-gray-3;
      border-radius: 50%;
      cursor: pointer;
    }

  }

  &__cards {

  }

  &__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }

  &__item {
    & > ul {
      .set-flex();
    }

    @media screen and (max-width: 1000px) {
      grid-column: span 3;
    }


  }

  &__card {

    &-bg {
      flex-basis: 50%;
    }

    &-sq {
      flex-basis: 50%;
    }

    &-wd {
      flex-basis: 100%;
    }
    
    &-bg > .adv__picture-container > .ibg {
      padding-bottom: 200%;
    }

    &-sq > .adv__picture-container > .ibg {
      padding-bottom: 100%;
    }

    &-wd > .adv__picture-container > .ibg {
      padding-bottom: 50%;
    }

  }

  &__logo-container {
    text-align: center;
    & > p {
     .set-font(14px, 700, @color: #ffffff);
     letter-spacing: 2.8px;
     text-transform: uppercase;
    }
  }


  // footer
  &__footer {
    background-color: @color-black-2;
    .set-flex(nowrap; center; center);
    padding: 75px 0;

    & a {
      //display: block;
      .set-font(14px; 700; @color: #ffffff);
      text-transform: uppercase;
      letter-spacing: 2.8px;
      border: 1px solid rgba(255, 255, 255, 0.5);
      padding: 9px 56px;


    }

  }









}