@import '../common/vars.less';
@import '../common/mixins.less';

.footer {
  background-color: @color-white-2;

  &__wrapper {
    .set-flex(@content: space-between, @align: center);
    padding: 99px 0px 103px 0px;

    @media screen and (max-width: 600px) {
      flex-direction: column;
      justify-content: flex-start;
      padding: 50px 0px 50px 0px;

      & ul {
        margin-bottom: 30px;
      }
    }
  
  }

  &__contacts {

    .set-font(14px; @line: 17.87px; @color: @color-gray-1);

    li:first-child {
      font-size: 16px;
      font-weight: 700;
    }

    li:not(:first-child) {
      font-style: italic
    }

    @media screen and (max-width: 600px) {
      li {
        text-align: center;
      }
    }
    
  }

  &__social{
    .set-flex();

    li:not(:last-child){
      margin-right: 15px;
    }
  }

  &__dev {
    .set-flex(@align: center);

    p {
      .set-font(14px; @style: italic; @line: 17.87px; @color: @color-gray-1);
      margin-right: 9px;
    }
  }


}