.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  /* IE6, IE7 */
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden;
}
.ibg {
  background-repeat: no-repeat !important;
  background-position: center !important;
  background-size: cover !important;
  width: 100% !important;
  height: 100% !important;
}
