}
.container {
  max-width: 1130px;
  padding: 0 15px;
  margin: 0 auto;
}
.card-title {
  font-family: 'Lato';
  font-size: 18px;
  font-weight: 700;
  font-style: normal;
  line-height: 12px;
  color: #353738;
