  text-transform: uppercase;
}
@media screen and (max-width: 1000px) {
  .card-title {
    font-size: 15px;
    letter-spacing: 2px;
  }
}
.picture-container {
  position: relative;
  height: 100%;
}
.picture-container__logo a {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(100, 194, 158, 0.5);
  cursor: pointer;
  opacity: 0;
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  transition: opacity 0.2s ease;
}
.picture-container__logo a:hover {
  opacity: 1;
}

.burger {
  position: absolute;
  width: 40px;
  height: 28px;
  top: 30px;
  right: 30px;
  cursor: pointer;
  display: none;
}
.burger__line {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  height: 2px;
  width: 100%;
  background-color: #4d4959;
  cursor: default;
  opacity: 1;
}
.burger__line:nth-child(2) {
  top: 50%;
}
.burger__line:last-child {
  top: 100%;
}
@media screen and (max-width: 768px) {
  .burger {
    display: block;
  }
}

.footer {
  background-color: #f7f7f7;
}
.footer__wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  padding: 99px 0px 103px 0px;
}
@media screen and (max-width: 600px) {
  .footer__wrapper {
    flex-direction: column;
