.header {
  position: absolute;
  top: 0;
  height: 0;
  width: 100%;
}
@media screen and (max-width: 768px) {
  .header::before {
    content: '';
    position: absolute;
    width: 80px;
    height: 72.34042553px;
    top: 30px;
    left: 50%;
    transform: translateX(-50%);
    background: url(img/svg/logo.svg) no-repeat;
    background-size: contain;
  }
  .header__nav {
    position: fixed;
    top: 0;
    height: 0;
    width: 100%;
    height: 100%;
    background-color: #222429;
    z-index: 999;
    opacity: 0;
    transform: translateX(100%);
    transition: all 0.2s linear;
  }
  .header__nav .container {
    display: flex;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
    height: 100%;
  }
  .header__nav .menu {
    display: block;
    padding: 0;
  }
  .header__nav .menu__logo {
    position: inherit;
    display: block;
    text-align: center;
    margin: 100px 0;
  }
  .header__nav .menu__list {
    display: block;
    text-align: center;
  }
  .header__nav .menu__list .menu__item {
    margin: 0;
  }
  .header__nav .menu__list .menu__item:first-child {
    margin-bottom: 100px;
  }
  .header__nav .menu__list .menu__item .menu__link {
    font-size: 32px;
    font-weight: 400;
    color: #f7f7f7;
  }
  .header__nav .menu__list .menu__item:nth-child(2) {
    display: none;
  }
  .header__nav .burger {
    display: none;
  }
  .header__nav .header__close {
    position: absolute;
    width: 40px;
    height: 40px;
    top: 30px;
    right: 30px;
    cursor: pointer;
  }
  .header__nav .header__close-line {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    height: 2px;
    width: 100%;
    background-color: #f7f7f7;
    cursor: default;
    opacity: 1;
    top: 50%;
  }
  .header__nav .header__close-line:first-child {
    transform: rotate(45deg);
  }
  .header__nav .header__close-line:last-child {
    transform: rotate(-45deg);
  }
  .header__nav .header__close:hover {
    opacity: 0.7;
  }
  .header__nav_active {
    opacity: 1;
    transform: translateX(0);
  }
}
@media screen and (max-width: 768px) and (max-height: 670px) {
  .header__nav .menu__logo {
    margin: 70px;
  }
}
@media screen and (max-width: 768px) and (max-height: 650px) {
