@import '../../common/vars.less';
@import '../../common/mixins.less';

.sign {
  padding-top: 109px;
  padding-bottom: 111px;
  text-align: center;

  @media screen and (max-width: 600px) {
    padding: 70px 0px 82px 0px;
    
  }

  &-form {
    &__logo {
      margin-bottom: 31px;
    }

    &__slogan {
      .set-font(24px; @style: italic; @line: 34px; @color: @color-gray-1);
      margin-bottom: 29px;
    }

    &__field {
      width: 400px;
      height: 69px;
      background-color: transparent;
      border: 1px solid @color-gray-5;
      padding: 28px 0 24px 28px;
      margin: 0 8px 0 0;

      .set-font(20px; @style: italic; @line: 24px; @color: @color-gray-4);
      @media screen and (max-width: 550px) {
        width: 300px;
        margin: 0 0 8px 0;
      }

    }

    &__submit {
      width: 126px;
      height: 69px;
      padding: 0;
      background-color: @color-green-10;
      border: 0;

      .font-lato();
      .set-font(24px; 300; @line: 24px; @color: @color-white-1);
      text-transform: uppercase;      

      &:hover {
        background-color: darken(@color-green-10, 10%);
      }

      @media screen and (max-width: 550px) {
        width: 300px;
      }

    }

  }

}