@import '../../common/vars.less';
@import '../../common/mixins.less';

@info-top-pading: 70px;

.item-padtop(@nth, @diff) {
  &:nth-child(@{nth}){
    & .intro__info {
      padding-top: @info-top-pading + @diff;
    }
  }
}

.logo-size(@nth; @size) {
  &:nth-child(@{nth}){
    .picture-container__logo a img{
      width: @size;       
    }
  }
}

.intro {

  // main screen
  &__main-screen {
    min-height: 100vh;
  }

  // intro__header
  &__header {
    padding: 117px 0 136px 0;
    background-color: @color-white-2;

    @media screen and (max-width: 1000px) {
      padding: 66px 0 90px;
    }

  }
  
  &__header-wrap {
    text-align: center;
  }
  
  &__header-logo {
    margin-bottom: 34px;
  }
  
  &__header-title {
    margin-bottom: 25px;

    @media screen and (max-width: 400px) {
      line-height: 26px;
    }
  }
  
  &__header-text {
    .set-font(@size: 18px; @line: 34px; @color: @color-gray-1);

    @media screen and (max-width: 1000px) {
      font-size: 16px;
      line-height: 28px;
    }
  }
  
  
  // intro__cards
  &__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);

  }

  &__item {
    .item-padtop(3, -2px) !important;
    .item-padtop(6, -11px) !important;

    @media screen and (max-width: 1400px) {
      .item-padtop(3, -30px) !important;
      .item-padtop(6, -30px) !important;
    }

    @media screen and (max-width: 1000px) {
      position: relative;
      grid-column: span 3;

      &:nth-child(5),
      &:nth-child(7) {
        padding-bottom: 70%;
      }
      
      // logo size
      // .logo-size(1, 12%);
      // .logo-size(4, 18%);
      // .logo-size(5, 21%);
      // .logo-size(7, 21%);

      //column order
      &:nth-child(5){
        grid-row-start: 6;
      }

      &:nth-child(6){
        grid-row-start: 5;
      }

    }


  }
  
  &__item_bg {
    grid-column: span 2;

    @media screen and (max-width: 1000px) {
      grid-column: span 3;
      padding-bottom: 70%;
    }

  }

  &__picture-container {

    @media screen and (max-width: 1000px) {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }

  }

  //intro__info
  &__info {
    position: relative;
    text-align: center;
    padding: @info-top-pading 0 81px 0;

    @media screen and (max-width: 1400px) {
      padding: 40px 0 52px 0;
    }

    &-logo{
      margin-bottom: 31px;
    }

    &-title {
      margin-bottom: 26px;
    }
    
    &-text {
      text-align: justify;
      padding: 0 66px;

      @media screen and (max-width: 1400px) {
        padding: 0 40px;
      }
  

    }

    &::after {
      content: '';
      position: absolute;
      display: block;
      left: 50%;
      bottom: 40px;
      width: 50px;
      height: 1px;
      background-color: @color-gray-1;
      transform: translateX(-50%);

      @media screen and (max-width: 1400px) {
        width: 30px;
        bottom: 30px;
      }

    }

    &::before {
      content: '';
      position: absolute;
      left: 50%;
      transform: translate(-50%, 100%);
      bottom: 0;
      .arrow(down, 10px, #fff)
    }

  }

  &__picture_sm {
    padding-bottom: 37.5%;

  }



}