@import './vars.less';

.font-lato() {
  font-family: @font-lato;
};

.font-myriad() {
  font-family: @font-myriad;
};

.set-font(
  @size: @font-size;
  @weight: @font-weight;
  @style: @font-style;
  @line: @line-height;
  @color: @color-gray-1) 
{
    font-size: @size;
    font-weight: @weight;
    font-style: @style;
    line-height: @line;
    color: @color;
};


.set-flex(
  @wrap: wrap;
  @content: flex-start;
  @align: stretch) 
{
  display: flex;
  flex-wrap: @wrap;
  justify-content: @content;
  align-items: @align;
}


.link-hover-opc(@val: .7) {
  opacity: @val;
}


// arrow
.arrow(@_, @size, @color) {
  width: 0;
  height: 0;
  border: @size solid transparent;
}

.arrow(up, @size, @color) {
  border-bottom-color: @color;
  border-top: 0;
}

.arrow(down, @size, @color) {
  border-top-color: @color;
  border-bottom: 0;
}  

.arrow(left, @size, @color) {
  border-right-color: @color;
  border-left: 0;
}

.arrow(right, @size, @color) {
  border-left-color: @color;
  border-right: 0;
}



//absolute
.set-absolute(
  @bgc; 
  @height: 100%; 
  @width: 100%; 
  @top: 0; 
  @left: 0;
  @cursor: default;
  @opc: 0) 
{
  content: '';
  position: absolute;
  top: @top;
  left: @left;
  height: @height;
  width: @width;
  background-color: @bgc;
  cursor: @cursor;
  opacity: @opc;
}


// absolute link
.set-pic-link(@sec: .2) {
  display: block;
  position: relative;
  width: 100%;
  height: 100%;

  &::after {
    .set-absolute(@color-green-1; @cursor: pointer);
    transition: opacity ~'@{sec}s' ease;
  }
  
  &:hover::after {
    .link-hover-opc(.5);
  }

}

// logo link
.set-logo-link (@sec: .2) {
  .set-absolute(@color-green-1; @cursor: pointer);
  .set-flex(nowrap, center, center);
  transition: opacity ~'@{sec}s' ease;

  &:hover {
    .link-hover-opc(1);
  }
}