@import 'vars.less';
@import 'mixins.less';

@size: 40px;

.burger {
  position: absolute;
  width: @size;
  height: @size - 12;
  top: @size - 10;
  right: @size - 10;
  cursor: pointer;
  display: none;

  &__line {
    .set-absolute(@bgc: @color-black-4; @height: 2px; @opc: 1);

    &:nth-child(2) {
      top: 50%;
    }

    &:last-child {
      top: 100%;
    }
  }

  @media screen and (max-width: 768px) {
    display: block; 
  }

}